@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;500;700&display=swap');

* {
  box-sizing: border-box;
  font-family: 'Oswald', sans-serif;
}

header {

  height: 100px;
  
}

section {
  color:white;
  min-height:80vh;
  position: relative;
  background-color: black;
}
label, input {
  color: #292e33;
}

footer {
  
  display: flex;
  justify-content: space-around;
  align-items: center;
background-color: black;
  color: white;
  height:10vh;
}



.show-error {
  position: absolute;
  left:50%;
  top: 50vh;
  transform: translate(-50%,-50%);
  
 
  
  z-index: 1;
  width:50%;
  /* margin: 10px auto; */
  text-align:center;
  color:red;
  background-color: rgba(0, 0, 0, 0.924);
  font-size:2em;
  border: 2px solid gray;
  border-radius: 20px;
}

.footer-icons a {
 font-size:40px;
 padding-left: 10px;
}